@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono:400,400i|IBM+Plex+Sans+Condensed:400,400i|IBM+Plex+Sans:100,100i,400,400i,700,700i|IBM+Plex+Serif:400,400i);
.App {
  height: 100vh;
  font-family: "IBM Plex Sans", sans-serif; }

.main-area {
  padding: 20px; }
  .main-area h1,
  .main-area h3 {
    text-align: center; }
  .main-area h1 {
    padding: 20px 0 30px 0;
    text-decoration: underline; }
  .main-area h3 {
    padding: 5px 20px 30px 20px; }

.sidebar {
  height: 100vh;
  border-left: 1px solid black;
  padding: 5px; }
  @media (max-width: 991px) {
    .sidebar {
      border-left: none; } }

#staff-area h1 {
  text-align: center;
  text-decoration: underline;
  padding: 10px; }

#staff-area .staff-profile {
  display: grid;
  grid-auto-flow: column;
  grid-column-gap: 0px;
  grid-template-columns: 1fr 1fr; }

#staff-area .indicator {
  height: 20px;
  width: 40px;
  opacity: 0.7;
  justify-self: center; }

#staff-area .staff-name {
  justify-self: flex-start; }

@media (max-width: 991px) {
  #staff-area .staff-name {
    margin-left: 10px; } }

#guest-area h1 {
  text-align: center;
  text-decoration: underline;
  padding: 10px; }

.guest-profile {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }
  .guest-profile .guest-name {
    padding-right: 10px; }
  .guest-profile .delete-button {
    color: red;
    opacity: 0.5;
    border: none;
    background: none;
    margin-top: -15px;
    border: none;
    outline: none; }
    .guest-profile .delete-button:hover {
      opacity: 1;
      cursor: pointer;
      -webkit-transform: scale(1.4, 1.4);
              transform: scale(1.4, 1.4); }

@media (max-width: 991px) {
  #guest-area h1 {
    padding: 15px 0 15px 0; } }

#login-form {
  margin-top: 30px;
  padding: 40px; }
  #login-form input:required {
    box-shadow: none; }
  #login-form input:invalid {
    box-shadow: none; }

#error-span {
  text-align: center; }
  #error-span span {
    color: red; }

#submit-button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 30px; }
  #submit-button Button {
    background-color: #9FBCBD;
    border: none; }

